.listItem {
    margin-bottom: 20px;
}

.listItemText {
    margin-right: 10px;
}

.button {
    margin-left: 20px;
    width: 100px;
    height: 30px;
    border: none;
    border-radius: 10px;

    border: 1px solid rgb(60, 58, 58);
}

.button:hover,
.button:focus {
    background-color: rgb(91, 23, 238);
    color: #fff;
    border-color: transparent;
    cursor: pointer;

    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}